
.news-item {
  width: calc(100%/3);
  padding: 0 0.4rem;
  margin-bottom: 0.8rem;
  display: inline-block;
  vertical-align: top;
}
.cover-img, .news-list{
  width: 100%;
}
.el-carousel__container {
  padding-bottom: 40%;
}
.cover-img-div {
  position: relative;
  background-size: cover;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat; 
  background-position: center;
}
.staggered-transition {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
  overflow: hidden;
  margin: 0;
  height: 20px;
}
.staggered-enter, .staggered-leave {
  opacity: 0;
  height: 0;
}
































































































































































.slicknav_hidden {
  display: none;
}
.logo-img {
  width: 200px;
}
.slicknav_arrow {
  font-size: 8px;
}

.album-box {
  width: 255px;
  height: 255px;
}
.album-cover {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}































































































































